<template>
  <div class="hello">
    <div class="holder">
      <form @submit.prevent="addSkill">
        <transition name="alert-in" enter-active-class="animated flipInX" leave-active-class="animated flipOutX">
          <p class="alert" v-if="errors.has('skill')">{{ errors.first('skill') }}</p>
        </transition>
        <input type="text" placeholder="Enter a skill you have.." v-model="skill" v-validate="'min:5'" name="skill">
      </form>
      <ul>
        <transition-group name="list" enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown">
          <li v-for="(item,index) in skills" :key='index'> {{ index }} . {{item.name}} 
            <button class="btn" v-on:click="deleteItem(item.id)"> Delete </button>
          </li>
        </transition-group>
      </ul>
      <p v-if="this.skills.length>0">These are the skills that you possess.</p>
      <p v-else>You don't posses any skills. :(</p>
    </div>
  </div>
</template>

<script>
import Utils from '../Utils.js';
import Position from '../../model/Position.js';
import { EventBus } from '../../event/Event.js';
import axios from 'axios';

export default {
  name: 'Skills',
  data(){
    return {
      skill:'',
      skills:[
      ]
    }
  },
  mounted : function (){
    EventBus.emit('loading-event',true);
    axios.get(Utils.BASEURL+'users')
      .then(response => {
        this.skill = '';
        this.skills=[];
        response.data.forEach((item)=>this.skills.push(new Position(item)));
        EventBus.emit('loading-event',false)
      })
  },
  methods : {
    deleteItem(index){ 
      EventBus.emit('loading-event',true);
      axios.delete(Utils.BASEURL+'user/'+index)
      .then( () => {
        axios.get(Utils.BASEURL+'users')
        .then(response => {
          this.skill = '';
          this.skills=[];
          response.data.forEach((item)=>this.skills.push(new Position(item)));
          EventBus.emit('loading-event',false)
        })
      });
    },
    addSkill(){
      this.$validator.validateAll().then((result) => {
        if (result) {
          EventBus.emit('loading-event',true);
          axios.post(Utils.BASEURL+'user',{name:this.skill})
          .then( () => {
            axios.get(Utils.BASEURL+'users')
            .then(response => {
              this.skill = '';
              this.skills=[];
              response.data.forEach((item)=>this.skills.push(new Position(item)));
              EventBus.emit('loading-event',false)
            })
          });
        } else {
          this.$notify({
            group: 'notifications',
            title: 'Warning',
            text: 'Not valid input',
            // type: 'error'
            // type: 'success'
            type: 'warn'
          });
        }
      })
    }
  }
}
</script>

